header {
	position: relative; }

.navi {
	width: 100%;
	height: 50px;
	background-color: #212121;
	&_menu {
		padding: 13px 70px;
		margin: 0;
		list-style: none;
		display: flex;
		justify-content: space-between; }
	&_link {
		font-size: 15px;
		font-weight: 300;
		color: #ffffff;
		&:hover {
			text-decoration: none;
			color: #ffffff; } }
	&_title {
		border-right: 2px dotted black;
		padding-right: 27px;
		&:last-child {
			border-right: none;
			padding-right: 20px; } } }

.info, .subheader {
	height: 84px;
	background-color: rgba(0, 0, 0, .6); }

.hamburger {
	display: none; }

.subheader {
	margin-top: 50px;
	position: absolute;
	width: 100%;
	&_descr {
		margin-top: 6px;
		font-size: 13px;
		font-weight: 100;
		text-align: left;
		color: rgba(255, 255, 255, 0.5); }
	&_logo {
		display: block;
		min-width: 100%; //максимальная ширина которую занимает элимент
		margin-top: 16px; }
	&_call {
		margin-top: 23px;
		font-size: 11px;
		font-weight: 300;
		text-align: right;
		color: #ffffff; }
	&_phone {
		display: block;
		margin-top: 8px;
		font-size: 20px;
		text-align: right;
		color: #ffffff;
		line-height: 12px;
		font-weight: 400;
		&:hover {
			text-decoration: none;
			color: #ffffff; } }
	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.12);
		border: none;
		background-image: linear-gradient(to top, #1eacc7, #4ce2ff);
		font-size: 14px;
		text-align: center;
		color: #ffffff;
		text-transform: uppercase;
		border-radius: 4px;
		@include btn_hover; } }

.a_visible { //скрываем ссылку
	display: none;

	.col-md-2.offset-md-2 {
		margin-left: 155px; } }

