.app {
	min-height: 860px;
	padding: 38px 0 79px 0;
	background: url("../src/img/grid_fon.jpg") center center/cover no-repeat;
	&_block {
		width: 112px;
		height: 24px;
		background-color: #000000;
		text-transform: uppercase;
		font-size: 11px;
		font-weight: 100;
		text-align: center;
		color: #ffffff;
		margin: 0 auto;
		padding: 5px; }
	&_title {
		margin-top: 38px;
		text-transform: uppercase;
		font-size: 38px;
		font-weight: bold;
		text-align: center;
		color: #222222; }
	&_subtitle {
		margin-top: 19px;
		margin-bottom: 32px;
		font-size: 17px;
		font-weight: normal;
		text-align: center;
		color: #222222; }
	&_img {
		position: relative;
		// width: 100%
		// z-index: -1
		padding-bottom: 19px; }
	&_round {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		bottom: 54px;
		left: 50%;
		transform: translateX(-110%);
		width: 26px;
		height: 26px;
		background-color: #000000;
		border-radius: 100%;
		cursor: pointer;
		margin: 0 auto;
		// text-align: center
		// top: 221px
		// left: 164px
		&:before {
			content: '';
			display: block;
			width: 14px;
			height: 2px;
			background-color: $main_color;
			transition: 0.9s all; }
		&:after {
			position: absolute;
			left: 49%;
			content: '';
			display: block;
			width: 2px;
			height: 14px;
			background-color: $main_color;
			transition: 0.9s all; }
		&:hover {
			&:before, &:after {
				background-color: #fff; } } }
	&_descr {
		position: absolute;
		font-size: 24px;
		font-weight: normal;
		text-align: center;
		color: #ffffff;
		text-transform: uppercase;
		// z-index: 2
		width: 100%;
		top: 77px;
		left: -14px; }

