@media (max-width: 575px) { //ставим 320
	.navi {
		height: 26px;
		&_menu {
			flex-direction: column;
			justify-content: center;
			align-items: center;
			position: fixed;
			top: 0;
			left: -100%; // чтоб меню ушло из видимости
			width: 70%;
			height: 100vh;
			padding: 20px 15px;
			background-color: rgba(0,0,0,0.8);
			transition: 0.8s all;
			z-index: 1;
			&_active {
				left: 0; } } // возвращаем меню
		&_title {
			padding-right: 0;
			border: none;
			margin-top: 30px;
			width: 100%;
			text-align: center;
			&:last-child {
				padding-right: 0; } }
		&_link {
			font-size: 16px;
			display: block; } }

	.hamburger {
		display: block;
		position: absolute;
		right: 10px;
		top: 50%;
		transform: translateY(-36%);
		width: 20px;
		span {
			display: block;
			width: 100%;
			height: 1px;
			background: #ffffff;
			margin-bottom: 4px;
			transition: 0.6s all; }
		&_active { // анимация
			span {
				margin-bottom: -1px;
				&:nth-child(1) {
					transform: rotate(-45deg); }
				&:nth-child(2) {
					display: none; }
				&:nth-child(3) {
					transform: rotate(45deg); } } } }

